.products-wrapp {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: justify;
        justify-content: space-between;
    -ms-flex-align: start;
        align-items: flex-start;
    text-align: center;
}
.products li {
    list-style: none;
    border: 1px solid #eee;
}
.buy {
    border: 1px solid black;
    background: black;
    cursor: pointer;
    padding: 4px;
    color: white;
}
.products li img { width: 100%; }
@media only screen and (max-width : 1300px) {
    .products-wrapp {
        -ms-flex-wrap: wrap;
            flex-wrap: wrap;
    }
}
@media only screen and (max-width : 768px) {
    .products-wrapp {
        -ms-flex-pack: center;
            justify-content: center;
        text-align: center;
    }
}


